<template>
  <div class="container">
    <div class="row">
      <div class="col">
        <div class="card shadow-sm">
          <div class="card-body px-0">
            <h1 class="card-title">All Characters</h1>
            <hr />

            <table v-if="this.ready" class="table table-striped table-hover">
              <thead>
                <tr>
                  <th>Character Name</th>
                  <th>Class &#38; Level</th>
                  <th>Race</th>
                  <th>Background</th>
                  <th>Alignment</th>
                  <th>Experience Points</th>
                  <th>Player Name</th>
                </tr>
              </thead>

              <tbody>
                <tr v-for="character in this.characters" v-bind:key="character.id">
                  <td>
                    <router-link class="btn btn-dark border-0" :to="`/admin/characters/${character.id}`">{{ character.character_name }}
                    </router-link>
                  </td>
                  <td>
                    {{ character.class }} {{ character.level }}
                  </td>
                  <td>
                    {{ character.race }}
                  </td>
                  <td>
                    {{ character.background }}
                  </td>
                  <td>
                    {{ character.alignment }}
                  </td>
                  <td>
                    {{ character.experience_points }}
                  </td>
                  <td>
                    {{ character.user.first_name }} {{ character.user.last_name }}
                  </td>
                </tr>
              </tbody>
            </table>
            <p v-else>Loading...</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Security from "./security";
import notie from "notie";

export default {
  data() {
    return {
      characters: [],
      ready: false,
    };
  },
  beforeMount() {
    fetch(process.env.VUE_APP_API_URL + "/characters", Security.requestOptions(""))
      .then((response) => response.json())
      .then((response) => {
        if (response.error) {
          notie.alert({
            type: "error",
            text: response.message,
          });
        } else {
          this.characters = response.data.characters;
          this.ready = true;
        }
      })
      .catch((error) => {
        notie.alert({
          type: "error",
          text: error,
        });
      });
  },
};
</script>

