<template>
  <nav class="navbar navbar-expand-lg navbar-dark bg-dark shadow-sm">
    <div class="container-fluid">
      <router-link class="navbar-brand" to="/">The Tavern</router-link>
      <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarNav">
        <ul class="navbar-nav d-flex align-items-center w-100">

          <li class="nav-item">
            <router-link class="nav-link" aria-current="page" to="/characters">
              Characters
            </router-link>
          </li>

          <li class="nav-item">
            <router-link class="nav-link" to="/admin/characters/0">
              Add Character
            </router-link>
          </li>

          <li v-if="store.token !== ''" class="nav-item dropdown">
            <a class="nav-link dropdown-toggle" href="#" id="navBarDropDown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
              Admin
            </a>

            <ul class="dropdown-menu" aria-labelledby="navBarDropDown">

              <li>
                <router-link class="dropdown-item" to="/admin/users">
                  Manage Users
                </router-link>
              </li>

              <li>
                <router-link class="dropdown-item" to="/admin/users/0">
                  Add User
                </router-link>
              </li>

            </ul>

          </li>

          <li class="nav-item m-auto"></li>

          <li class="nav-item">
            <router-link v-if="store.token == ''" class="nav-link" aria-current="page" to="/login">Login</router-link>
            <a v-else class="nav-link" href="javascript:void(0);" @click="logout">Logout</a>
          </li>

        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
import { store } from "./store.js";
import router from "./../router/index.js";
import Security from "./security.js";

export default {
  data() {
    return {
      store,
      currentRoute: this.$route.name,
    };
  },
  methods: {
    logout() {
      const payload = {
        token: store.token,
      };

      fetch(process.env.VUE_APP_API_URL + "/users/logout", Security.requestOptions(payload))
        .then((response) => response.json())
        .then((response) => {
          if (response.error) {
            console.log(response.message);
          } else {
            store.token = "";
            store.user = {};

            document.cookie = "_site_data=; Path=/; SameSite=Strict; Secure; Expires=Thu, 01 Jan 1970 00:00:01 GMT;";

            router.push("/login");
          }
        });
    },
  },
};
</script>

<style scoped>
.navbar-brand {
  font-family: "Pacifico", sans-serif;
  font-size: 2em;
  color: antiquewhite;
}

.active.dropdown-item,
a:active {
  background-color: #212529;
}
</style>