<template>
  <div class="container">
    <div class="row">
      <div class="col d-flex justify-content-center align-items-center">
        <div class="card shadow-sm">
          <div class="card-body">
            <h1 class="card-title">Edit Character</h1>
            <hr />
            <form-tag v-if="this.ready" @characterEditEvent="submitHandler" name="characterform" event="characterEditEvent">
              <div class="row">
                <div class="col">
                  <text-input v-model="character.character_name" type="text" required="true" label="Character Name" :value="character.character_name" name="character-name" />
                </div>
                <div class="col">
                  <text-input v-model="character.class" type="text" required="true" label="Class" :value="character.class" name="class" />
                </div>
                <div class="col">
                  <number-input v-model="character.level" type="number" required="true" label="Level" :value="character.level" name="level" />
                </div>
                <div class="col">
                  <text-input v-model="character.race" type="text" required="true" label="Race" :value="character.race" name="race" />
                </div>
              </div>
              <div class="row">
                <div class="col">
                  <text-input v-model="character.background" type="text" required="true" label="Background" :value="character.background" name="background" />
                </div>
                <div class="col">
                  <text-input v-model="character.alignment" type="text" required="true" label="Alignment" :value="character.alignment" name="alignment" />
                </div>
                <div class="col">
                  <number-input v-model="character.experience_points" type="number" required="true" label="Experience Points" :value="character.experience_points" name="experience-points" />
                </div>
                <div class="col">
                  <div class="mb-3 text-start">
                    <label class="form-label">Player Name</label>
                    <p class="ms-3 mt-2 text-muted">{{ character.user.first_name }} {{ character.user.last_name }}</p>
                  </div>
                </div>
              </div>
              <hr>

              <div class="row mt-5">
                <div class="col-4 d-flex flex-column justify-content-between">
                  <div class="row">
                    <div class="col">
                      <number-input v-model="character.strength" type="number" required="true" label="Strength" :value="character.strength" name="strength" />
                    </div>
                    <div class="col text-start">
                      <div class="form-check">
                        <input v-model="character.athletics" true-value="1" false-value="0" class="form-check-input" type="checkbox" id="athletics" :value="character.athletics">
                        <label class="form-check-label" for="athletics">
                          Athletics
                        </label>
                      </div>
                    </div>
                  </div>
                  <hr>

                  <div class="row">
                    <div class="col">
                      <number-input v-model="character.dexterity" type="number" required="true" label="Dexterity" :value="character.dexterity" name="dexterity" />
                    </div>
                    <div class="col text-start">
                      <div class="form-check">
                        <input v-model="character.acrobatics" true-value="1" false-value="0" class="form-check-input" type="checkbox" id="acrobatics" :value="character.acrobatics">
                        <label class="form-check-label" for="acrobatics">
                          Acrobatics
                        </label>
                      </div>
                      <div class="form-check">
                        <input v-model="character.sleight_of_hand" true-value="1" false-value="0" class="form-check-input" type="checkbox" id="sleight_of_hand" :value="character.sleight_of_hand">
                        <label class="form-check-label" for="sleight_of_hand">
                          Sleight of Hand
                        </label>
                      </div>
                      <div class="form-check">
                        <input v-model="character.stealth" true-value="1" false-value="0" class="form-check-input" type="checkbox" id="stealth" :value="character.stealth">
                        <label class="form-check-label" for="stealth">
                          Stealth
                        </label>
                      </div>
                    </div>
                  </div>
                  <hr>

                  <div class="row">
                    <div class="col">
                      <number-input v-model="character.constitution" type="number" required="true" label="Constitution" :value="character.constitution" name="constitution" />
                    </div>
                    <div class="col text-start">

                    </div>
                  </div>
                  <hr>
                  <div class="row">
                    <div class="col">
                      <number-input v-model="character.intelligence" type="number" required="true" label="Intelligence" :value="character.intelligence" name="intelligence" />
                    </div>
                    <div class="col text-start">
                      <div class="form-check">
                        <input v-model="character.arcana" true-value="1" false-value="0" class="form-check-input" type="checkbox" id="arcana" :value="character.arcana">
                        <label class="form-check-label" for="arcana">
                          Arcana
                        </label>
                      </div>
                      <div class="form-check">
                        <input v-model="character.history" true-value="1" false-value="0" class="form-check-input" type="checkbox" id="history" :value="character.history">
                        <label class="form-check-label" for="history">
                          History
                        </label>
                      </div>
                      <div class="form-check">
                        <input v-model="character.investigation" true-value="1" false-value="0" class="form-check-input" type="checkbox" id="investigation" :value="character.investigation">
                        <label class="form-check-label" for="investigation">
                          Investigation
                        </label>
                      </div>
                      <div class="form-check">
                        <input v-model="character.nature" true-value="1" false-value="0" class="form-check-input" type="checkbox" id="nature" :value="character.nature">
                        <label class="form-check-label" for="nature">
                          Nature
                        </label>
                      </div>
                      <div class="form-check">
                        <input v-model="character.religion" true-value="1" false-value="0" class="form-check-input" type="checkbox" id="religion" :value="character.religion">
                        <label class="form-check-label" for="religion">
                          Religion
                        </label>
                      </div>
                    </div>
                  </div>
                  <hr>

                  <div class="row">
                    <div class="col">
                      <number-input v-model="character.wisdom" type="number" required="true" label="Wisdom" :value="character.wisdom" name="wisdom" />
                    </div>
                    <div class="col text-start">
                      <div class="form-check">
                        <input v-model="character.animal_handling" true-value="1" false-value="0" class="form-check-input" type="checkbox" id="religion" :value="character.religion">
                        <label class="form-check-label" for="animal_handling">
                          Religion
                        </label>
                      </div>
                      <div class="form-check">
                        <input v-model="character.religion" true-value="1" false-value="0" class="form-check-input" type="checkbox" id="animal_handling" :value="character.animal_handling">
                        <label class="form-check-label" for="religion">
                          Animal Handling
                        </label>
                      </div>
                      <div class="form-check">
                        <input v-model="character.insight" true-value="1" false-value="0" class="form-check-input" type="checkbox" id="insight" :value="character.insight">
                        <label class="form-check-label" for="insight">
                          Insight
                        </label>
                      </div>
                      <div class="form-check">
                        <input v-model="character.medicine" true-value="1" false-value="0" class="form-check-input" type="checkbox" id="medicine" :value="character.medicine">
                        <label class="form-check-label" for="medicine">
                          Medicine
                        </label>
                      </div>
                      <div class="form-check">
                        <input v-model="character.perception" true-value="1" false-value="0" class="form-check-input" type="checkbox" id="perception" :value="character.perception">
                        <label class="form-check-label" for="perception">
                          Perception
                        </label>
                      </div>
                      <div class="form-check">
                        <input v-model="character.survival" true-value="1" false-value="0" class="form-check-input" type="checkbox" id="survival" :value="character.survival">
                        <label class="form-check-label" for="survival">
                          Survival
                        </label>
                      </div>
                    </div>
                  </div>
                  <hr>

                  <div class="row">
                    <div class="col">
                      <number-input v-model="character.charisma" type="number" required="true" label="Charisma" :value="character.charisma" name="charisma" />
                    </div>
                    <div class="col text-start">
                      <div class="form-check">
                        <input v-model="character.deception" true-value="1" false-value="0" class="form-check-input" type="checkbox" id="deception" :value="character.deception">
                        <label class="form-check-label" for="deception">
                          Deception
                        </label>
                      </div>
                      <div class="form-check">
                        <input v-model="character.intimidation" true-value="1" false-value="0" class="form-check-input" type="checkbox" id="intimidation" :value="character.intimidation">
                        <label class="form-check-label" for="intimidation">
                          Intimidation
                        </label>
                      </div>
                      <div class="form-check">
                        <input v-model="character.performance" true-value="1" false-value="0" class="form-check-input" type="checkbox" id="performance" :value="character.performance">
                        <label class="form-check-label" for="performance">
                          Performance
                        </label>
                      </div>
                      <div class="form-check">
                        <input v-model="character.persuation" true-value="1" false-value="0" class="form-check-input" type="checkbox" id="persuation" :value="character.persuation">
                        <label class="form-check-label" for="persuation">
                          Persuation
                        </label>
                      </div>
                    </div>
                  </div>
                  <hr>


                  <div class="row d-flex flex-column">
                    <div class="col text-start">
                      <label class="form-label">Other Proficiencies and Languages</label>
                    </div>
                    <div class="col">
                      <textarea :v-model="character.other_proficiencies_and_languages" :value="character.other_proficiencies_and_languages" @input="character.other_proficiencies_and_languages = $event.target.value" class="form-control" rows="10"></textarea>
                    </div>
                  </div>
                </div>

                <div class="col">
                  <div class="row">
                    <div class="col">
                      <number-input v-model="character.armor_class" type="number" required="true" label="Armor Class" :value="character.armor_class" name="armor-class" />
                    </div>
                    <div class="col">
                      <number-input v-model="character.initiative" type="number" required="true" label="Initiative" :value="character.initiative" name="initiative" />
                    </div>
                    <div class="col">
                      <number-input v-model="character.speed" type="number" required="true" label="Speed" :value="character.speed" name="speed" />
                    </div>
                  </div>

                  <div class="row">
                    <div class="col">
                      <number-input v-model="character.current_hit_points" type="number" required="true" label="Current Hit Points" :value="character.current_hit_points" name="current-hit-points" />
                    </div>
                    <div class="col">
                      <number-input v-model="character.max_hit_points" type="number" required="true" label="Total Hit Points" :value="character.max_hit_points" name="max-hit-points" />
                    </div>
                  </div>

                  <number-input v-model="character.temporary_hit_points" type="number" required="true" label="Temporary Hit Points" :value="character.temporary_hit_points" name="temporary-hit-point" />

                  <div class="row">
                    <div class="col">
                      <text-input v-model="character.hit_dice" type="text" required="true" label="Hit Dice" :value="character.hit_dice" name="hit-dice" />
                    </div>
                    <div class="col">
                      <number-input v-model="character.death_save_successes" type="number" required="true" label="Death Save Successes" :value="character.death_save_successes" name="death-save-successes" />
                      <number-input v-model="character.death_save_failures" type="number" required="true" label="Death Save Failures" :value="character.death_save_failures" name="death-save-failures" />
                    </div>
                  </div>
                  <hr>

                  <div class="row">
                    <label class="form-label">Attacks And Spellcasting</label>
                    <textarea :v-model="character.attacks_and_spellcasting" :value="character.attacks_and_spellcasting" @input="character.attacks_and_spellcasting = $event.target.value" class="form-control" rows="20"></textarea>
                  </div>
                  <hr>

                  <div class="row">
                    <div class="col">
                      <number-input v-model="character.copper_pieces" type="number" required="true" label="Copper Pieces" :value="character.copper_pieces" name="copper-pieces" />
                      <number-input v-model="character.silver_pieces" type="number" required="true" label="Silver Pieces" :value="character.silver_pieces" name="silver-pieces" />
                      <number-input v-model="character.electrum_pieces" type="number" required="true" label="Electrum Pieces" :value="character.electrum_pieces" name="electrum-pieces" />
                      <number-input v-model="character.gold_pieces" type="number" required="true" label="Gold Pieces" :value="character.gold_pieces" name="gold-pieces" />
                      <number-input v-model="character.platinum_pieces" type="number" required="true" label="Platinum Pieces" :value="character.platinum_pieces" name="platinum-pieces" />
                    </div>
                    <div class="col-9 d-flex flex-column">
                      <label class="form-label">Equipment</label>
                      <textarea :v-model="character.equipment" :value="character.equipment" @input="character.equipment = $event.target.value" class="form-control" rows="14"></textarea>

                    </div>
                  </div>
                </div>

                <div class="col d-flex flex-column justify-content-between mx-2">
                  <div class="row d-flex flex-column text-start">
                    <div class="col">
                      <label class="form-label">Personality Traits</label>
                    </div>
                    <div class="col">
                      <textarea :v-model="character.personality" :value="character.personality" @input="character.personality = $event.target.value" class="form-control" rows="4"></textarea>
                    </div>
                  </div>
                  <div class="row d-flex flex-column text-start">
                    <div class="col">
                      <label class="form-label">Ideals</label>
                    </div>
                    <div class="col">
                      <textarea :v-model="character.ideals" :value="character.ideals" @input="character.ideals = $event.target.value" class="form-control" rows="4"></textarea>
                    </div>
                  </div>
                  <div class="row d-flex flex-column text-start">
                    <div class="col">
                      <label class="form-label">Bonds</label>
                    </div>
                    <div class="col">
                      <textarea :v-model="character.bonds" :value="character.bonds" @input="character.bonds = $event.target.value" class="form-control" rows="4"></textarea>
                    </div>
                  </div>
                  <div class="row d-flex flex-column text-start">
                    <div class="col">
                      <label class="form-label">Flaws</label>
                    </div>
                    <div class="col">
                      <textarea :v-model="character.flaws" :value="character.flaws" @input="character.flaws = $event.target.value" class="form-control" rows="4"></textarea>
                    </div>
                  </div>
                  <div class="row d-flex flex-column text-start">
                    <div class="col">
                      <label class="form-label">Features and Traits</label>
                    </div>
                    <div class="col">
                      <textarea :v-model="character.features_and_traits" :value="character.features_and_traits" @input="character.features_and_traits = $event.target.value" class="form-control" rows="30"></textarea>
                    </div>
                  </div>
                </div>
              </div>

              <hr />
              <div class="float-start">
                <input type="submit" class="btn btn-success me-2" value="Save" />
                <router-link to="/characters" class="btn btn-secondary me-2">Cancel</router-link>
              </div>
              <div class="float-end">
                <a v-if="this.$route.params.characterId > 0" class="btn btn-danger" href="javascript:void(0);" @click="confirmDelete(this.character.id)">
                  Delete
                </a>
              </div>
              <div class="clearfix"></div>
            </form-tag>
            <p v-else>Loading...</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Security from "./security";
import FormTag from "./forms/FormTag.vue";
import TextInput from "./forms/TextInput.vue";
import NumberInput from "./forms/NumberInput.vue";
import notie from "notie";
import { store } from "./store.js";
import router from "./../router/index.js";

export default {
  beforeMount() {
    Security.requireToken();

    if (parseInt(String(this.$route.params.characterId), 10) > 0) {
      // editing an existing user
      fetch(process.env.VUE_APP_API_URL + "/admin/characters/get/" + this.$route.params.characterId, Security.requestOptions(""))
        .then((response) => response.json())
        .then((data) => {
          if (data.error) {
            this.$emit("error", data.message);
          } else {
            this.character = data;
            this.ready = true;
          }
        })
        .catch((error) => {
          this.$emit("error", error);
        });
    } else {
      this.character.user_id = this.store.user.id;
      this.character.user.first_name = this.store.user.first_name;
      this.character.user.last_name = this.store.user.last_name;
      this.ready = true;
    }
  },
  data() {
    return {
      store,
      ready: false,
      character: {
        id: 0,
        user_id: 0,
        character_name: "",
        class: "",
        level: 0,
        background: "",
        race: "",
        alignment: "",
        experience_points: 0,
        strength: 0,
        dexterity: 0,
        constitution: 0,
        wisdom: 0,
        intelligence: 0,
        charisma: 0,
        acrobatics: 0,
        animal_handling: 0,
        arcana: 0,
        athletics: 0,
        deception: 0,
        history: 0,
        insight: 0,
        intimidation: 0,
        investigation: 0,
        medicine: 0,
        nature: 0,
        perception: 0,
        performance: 0,
        persuation: 0,
        religion: 0,
        sleight_of_hand: 0,
        stealth: 0,
        survival: 0,
        other_proficiencies_and_languages: "",
        armor_class: 0,
        initiative: 0,
        speed: 0,
        max_hit_points: 0,
        current_hit_points: 0,
        temporary_hit_points: 0,
        hit_dice: "",
        death_save_successes: 0,
        death_save_failures: 0,
        attacks_and_spellcasting: "",
        equipment: "",
        copper_pieces: 0,
        silver_pieces: 0,
        electrum_pieces: 0,
        gold_pieces: 0,
        platinum_pieces: 0,
        personality: "",
        ideals: "",
        bonds: "",
        flaws: "",
        features_and_traits: "",
        user: {
          first_name: "",
          last_name: "",
        },
      },
    };
  },
  components: {
    "form-tag": FormTag,
    "text-input": TextInput,
    "number-input": NumberInput,
  },
  methods: {
    submitHandler() {
      const payload = {
        id: parseInt(String(this.$route.params.characterId), 10),
        user_id: parseInt(String(this.character.user_id), 10),
        character_name: this.character.character_name,
        class: this.character.class,
        level: parseInt(String(this.character.level), 10),
        background: this.character.background,
        race: this.character.race,
        alignment: this.character.alignment,
        experience_points: parseInt(String(this.character.experience_points), 10),
        strength: parseInt(String(this.character.strength), 10),
        dexterity: parseInt(String(this.character.dexterity), 10),
        constitution: parseInt(String(this.character.constitution), 10),
        wisdom: parseInt(String(this.character.wisdom), 10),
        intelligence: parseInt(String(this.character.intelligence), 10),
        charisma: parseInt(String(this.character.charisma), 10),
        acrobatics: parseInt(String(this.character.acrobatics), 10),
        animal_handling: parseInt(String(this.character.animal_handling), 10),
        arcana: parseInt(String(this.character.arcana), 10),
        athletics: parseInt(String(this.character.athletics), 10),
        deception: parseInt(String(this.character.deception), 10),
        history: parseInt(String(this.character.history), 10),
        insight: parseInt(String(this.character.insight), 10),
        intimidation: parseInt(String(this.character.intimidation), 10),
        investigation: parseInt(String(this.character.investigation), 10),
        medicine: parseInt(String(this.character.medicine), 10),
        nature: parseInt(String(this.character.nature), 10),
        perception: parseInt(String(this.character.perception), 10),
        performance: parseInt(String(this.character.performance), 10),
        persuation: parseInt(String(this.character.persuation), 10),
        religion: parseInt(String(this.character.religion), 10),
        sleight_of_hand: parseInt(String(this.character.sleight_of_hand), 10),
        stealth: parseInt(String(this.character.stealth), 10),
        survival: parseInt(String(this.character.survival), 10),
        other_proficiencies_and_languages: this.character.other_proficiencies_and_languages,
        armor_class: parseInt(String(this.character.armor_class), 10),
        initiative: parseInt(String(this.character.initiative), 10),
        speed: parseInt(String(this.character.speed), 10),
        max_hit_points: parseInt(String(this.character.max_hit_points), 10),
        current_hit_points: parseInt(String(this.character.current_hit_points), 10),
        temporary_hit_points: parseInt(String(this.character.temporary_hit_points), 10),
        hit_dice: this.character.hit_dice,
        death_save_successes: parseInt(String(this.character.death_save_successes), 10),
        death_save_failures: parseInt(String(this.character.death_save_failures), 10),
        attacks_and_spellcasting: this.character.attacks_and_spellcasting,
        equipment: this.character.equipment,
        copper_pieces: parseInt(String(this.character.copper_pieces), 10),
        silver_pieces: parseInt(String(this.character.silver_pieces), 10),
        electrum_pieces: parseInt(String(this.character.electrum_pieces), 10),
        gold_pieces: parseInt(String(this.character.gold_pieces), 10),
        platinum_pieces: parseInt(String(this.character.platinum_pieces), 10),
        personality: this.character.personality,
        ideals: this.character.ideals,
        bonds: this.character.bonds,
        flaws: this.character.flaws,
        features_and_traits: this.character.features_and_traits,
      };

      fetch(`${process.env.VUE_APP_API_URL}/admin/characters/save`, Security.requestOptions(payload))
        .then((response) => response.json())
        .then((data) => {
          if (data.error) {
            this.$emit("error", data.message);
          } else {
            this.$emit("success", "Changes saved!");
            router.push("/characters");
          }
        })
        .catch((error) => {
          this.$emit("error", error);
        });
    },

    confirmDelete(id) {
      notie.confirm({
        text: "Are you sure you want to delete this character?",
        submitText: "Delete",
        submitCallback: () => {
          let payload = {
            id: id,
          };

          fetch(`${process.env.VUE_APP_API_URL}/admin/characters/delete`, Security.requestOptions(payload))
            .then((response) => response.json())
            .then((data) => {
              if (data.error) {
                this.$emit("error", data.message);
              } else {
                this.$emit("success", "Character deleted!");
                router.push("/characters");
              }
            });
        },
      });
    },
  },
};
</script>

<style scoped>
textarea {
  height: 100%;
  box-sizing: border-box;
  white-space: pre-line;
}
</style>