<template>
  <div class="container">
    <div class="row">
      <div class="col">
        <div class="card shadow-sm">
          <div class="card-body px-0">
            <h1 class="card-title">All Users</h1>
            <hr />

            <table v-if="this.ready" class="table table-striped table-hover">
              <thead>
                <tr>
                  <th>User</th>
                  <th>Email</th>
                  <th>Active</th>
                  <th>Status</th>
                </tr>
              </thead>

              <tbody>
                <tr v-for="user in this.users" v-bind:key="user.id">
                  <td>
                    <router-link class="btn btn-dark border-0" :to="`/admin/users/${user.id}`">{{ user.first_name }} {{ user.last_name }}
                    </router-link>
                  </td>
                  <td>
                    {{ user.email }}
                  </td>
                  <td v-if="user.active === 1">
                    <span class="badge bg-success">
                      Active
                    </span>
                  </td>
                  <td v-else>
                    <span class="badge bg-danger">
                      Inactive
                    </span>
                  </td>
                  <td v-if="user.token.id > 0">
                    <button class="badge bg-success border-0" @click="logUserOut(user.id)">
                      Logged in
                    </button>
                  </td>
                  <td v-else>
                    <span class="badge bg-danger">
                      Logged out
                    </span>
                  </td>
                </tr>
              </tbody>
            </table>
            <p v-else>Loading...</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Security from "./security";
import notie from "notie";
import { store } from "./store";

export default {
  data() {
    return {
      users: [],
      ready: false,
    };
  },
  beforeMount() {
    Security.requireToken();

    fetch(process.env.VUE_APP_API_URL + "/admin/users", Security.requestOptions(""))
      .then((response) => response.json())
      .then((response) => {
        if (response.error) {
          notie.alert({
            type: "error",
            text: response.message,
          });
        } else {
          this.users = response.data.users;
          this.ready = true;
        }
      })
      .catch((error) => {
        notie.alert({
          type: "error",
          text: error,
        });
      });
  },
  methods: {
    logUserOut(id) {
      if (id !== store.user.id) {
        notie.confirm({
          text: "Are you sure you want to logout this user?",
          submitText: "Log Out",
          submitCallback: () => {
            fetch(process.env.VUE_APP_API_URL + "/admin/log-user-out/" + id, Security.requestOptions(""))
              .then((response) => response.json())
              .then((response) => {
                if (response.error) {
                  this.$emit('error', response.message);
                } else {
                  this.$emit('success', response.message);
                  this.$emit('forceUpdate');
                }
              })
              .catch((error) => {
                this.$emit('error', error);
              });
          },
        });
      } else {
        this.$emit('error', "You can't log yourself out!")
      }
    },
  },
};
</script>