import { createRouter, createWebHistory } from 'vue-router'
import AppHome from './../components/AppHome.vue'
import TheTavern from './../components/TheTavern.vue'
import CharacterSheets from './../components/CharacterSheets.vue'
import CharacterSheet from './../components/CharacterSheet.vue'
import CharacterSheetEdit from './../components/CharacterSheetEdit.vue'
import UsersPage from './../components/UsersPage.vue'
import UserEdit from './../components/UserEdit.vue'
import AppLogin from './../components/AppLogin.vue'
import Security from '@/components/security'

const routes = [
    {
        path: '/',
        name: 'Home',
        component: AppHome,
    },
    {
        path: '/the-tavern',
        name: 'The Tavern',
        component: TheTavern,
    },
    {
        path: '/characters',
        name: 'Characters',
        component: CharacterSheets,
    },
    {
        path: '/characters/:characterId',
        name: 'Character',
        component: CharacterSheet
    },
    {
        path: '/admin/characters/:characterId',
        name: 'CharacterSheetEdit',
        component: CharacterSheetEdit
    },
    {
        path: '/admin/users',
        name: 'Users',
        component: UsersPage
    },
    {
        path: '/admin/users/:userId',
        name: 'User',
        component: UserEdit
    },
    {
        path: '/login',
        name: 'Login',
        component: AppLogin,
    },
]

const router = createRouter({ history: createWebHistory(), routes, linkActiveClass: "active", linkExactActiveClass: "active" })

router.beforeEach(() => {
    Security.checkToken();
})

export default router